import {mapState,mapMutations} from 'vuex'
import {SET_FINAL_DATA,SET_RUBBISH_SEARCH} from '../../../store/mutation-types.js'
export default {
	data() {
		return {
			list:''
		}
	},
	computed:{
		...mapState([
			'requesturl',
			'key'
		])
	},
	methods: {
		...mapMutations([
			SET_FINAL_DATA,
			SET_RUBBISH_SEARCH
		]),
		getAllhot:function(){
			uni.request({
				url:this.requesturl + '/txapi/hotlajifenlei/index?key=' + this.key,
				success:res=>{
					console.log(res)
					this.list = res.data.newslist
				}
			})
		},
		getres:function(e){
			console.log(1)
			var name = e.currentTarget.dataset.name;
			uni.request({
				url:this.requesturl + '/txapi/lajifenlei/index?key=' + this.key + '&word=' + name,
				success:(res)=>{
					console.log(2)
					console.log(res)
					this.SET_RUBBISH_SEARCH(res.data.newslist)
					uni.navigateTo({
						url:'../searchlist/searchlist'
					})
				}
			})
		}
	},
	onLoad() {
		this.getAllhot()
	}
}
